.gempa-container {
    padding-top: 20px;
    height: 900px;
    background-color: white;
}

.kanan {
    float: right;
    width: 45vw;
    margin: 10px 10px;
}

.kiri {
    float: left;
    width: 45vw;
    margin: 10px 10px;
}

.kiri>img {
    width: 45vw;
}

.info>table:nth-child(2)>tr:nth-child(odd) {
    background-color: #EEE;
}

.info>table:nth-child(2)>tr>td {
    padding: 10px;
}

.info>table:nth-child(2) {
    width: 80%;
}

.info>table:nth-child(2)>tr>td,
.info>table:nth-child(2) {
    border: solid 1px #BBB;
    border-collapse: collapse;
}

.info>table:nth-child(3) {
    width: 80%;
    margin-top: 20px;
    margin-bottom: 20px;
}

.info>table:nth-child(3)>tr>td:nth-child(1) {
    padding-left: 20px;
}

.info>table:nth-child(3)>tr>td:nth-child(2) {
    padding-left: 50px;
}

.daftar>table {
    width: 80%;
}

.daftar>table>tr>td {
    padding: 10px;
}

.daftar>table,
.daftar>table>tr>td,
.daftar>table>tr>th {
    border: solid 1px #BBB;
    border-collapse: collapse;
}

.daftar>table>tr:nth-child(odd),
.daftar>table>tr>th {
    background-color: #EEE;
}

@media(max-width:920px) {
    .kiri {
        float: none;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 0px;
    }

    .kiri>img {
        width: 100vw;
        margin: 0px;
    }

    .kanan {
        float: none;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    .gempa-container {
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    .daftar {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 0px;
    }

    .info {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 0px;
    }
}